import * as React from "react";
import { urlJoin, isSuccessStatusCode } from '../../tool';
import { Dialog } from "./Dialog";
import { AppInsightsService } from '../../services/AppInsightsService';
import { SubscriptionService } from '../../services/SubscriptionService';
import "./Dialog.sass"

interface IState 
{
  email: string;
  subscriptionError?: string;
  subscribing: boolean;
  acceptPrivacy: boolean;
  acceptPrivacyError?: string;
  emailValidationError?: string;
  captchaResponse: CaptchaResponse;
  captchaSelection: string;
}

interface IProps
{
  
}

export class SubscribeDialog extends React.Component<IProps, IState>
{
  dialog = React.createRef<Dialog>();
  input = React.createRef<HTMLInputElement>();
  onSuccess: (cnt: number) => any;
  
  showUpdate = false;
  
  constructor(props)
  {
    super(props);
    this.state = {
      email: "",
      subscribing: false,
      acceptPrivacy: false,
      captchaResponse: null,
      captchaSelection: null
    }
  }
  
  componentDidMount()
  {
    SubscriptionService.setSubscribeDialog(this);
  }
  
  componentDidUpdate()
  {
    if(this.showUpdate == true)
    {
      this.input?.current?.focus();
      this.showUpdate = false;
    }
  }
  
  async show()
  {
    this.showUpdate = true;
    this.dialog.current.show();
    await this.initCaptcha();
  }
  
  async initCaptcha()
  {
    try 
    {
      let res = await service.getCaptcha();
      this.setState({
        captchaResponse: res
      });
    }
    catch (error) 
    {
      console.error(error);
    }
  }
  
  hide()
  {
    this.setState({
      captchaResponse: null,
      captchaSelection: null
    });
    this.dialog.current.hide();
  }
  
  validate(): boolean
  {
    if(this.state.email == null || this.state.email == "")
    {
      this.setState({
        emailValidationError: "Bitte geben Sie eine E-Mail addresse ein."
      })
      return false;
    }
    if(this.state.acceptPrivacy == false)
    {
      this.setState({
        acceptPrivacyError: "Bitte akzeptieren Sie unsere Datenschutzbedingungen."
      }) 
    }
    return true;
  }
  
  onSubscribeClicked = async () => {
    this.setState({
      emailValidationError: null,
      subscriptionError: null,
      acceptPrivacyError: null,
      subscribing: true
    });
    
    try 
    {
      if(this.validate() == false)
        return;
        
      let r = await service.submitSubscription({
        recipient: {
          email: this.state.email,
        },
        captcha: {
          correctValue: this.state.captchaResponse.correctValue,
          keyValue: this.state.captchaResponse.keyValue,
          selected: this.state.captchaSelection,
          lists: []
        }
      });
      this.onSuccess(r);
    } 
    catch (error) 
    {
      if(error instanceof CaptchaValidationFailedError)
      {
        this.setState({
          subscriptionError: "Sie haben das falsche Captcha-Bild ausgewählt. Bitte versuchen Sie es erneut."
        });
      }
      else
      {
        this.setState({
          subscriptionError: "Oops, beim Anmeldevorgang ist ein Fehler passiert. Bitte versuche es später erneut oder kontaktieren Sie uns damit wir das Problem beheben können."
        });
        AppInsightsService.trackError("Error during newsletter subscription", error);
        console.error(error);
      }
    }
    finally
    {
      this.setState({
        subscribing: false
      });
    }
  }
  
  handleChange = async (ev: React.ChangeEvent<any>) => {
    let d = {};
    if(ev.target.type == "checkbox")
      d[ev.target.name] = ev.target.checked;
    else
      d[ev.target.name] = ev.target.value;
    this.setState(d);
  }
  
  render()
  {
    return (
      <Dialog ref={this.dialog} header={<div>Newsletteranmeldung</div>}>
        <div className="subscribe-dialog">
            <div>
              Wenn Sie über die neuesten Blogbeiträge von Durchblick durch Daten auf dem laufenden bleiben wollen, melden Sie sich hier bei unserem Newsletter an. 
            </div>
            <div className="field">
                <div className="control has-icons-left has-icons-right">
                    <input className={`input ${this.state.emailValidationError != null ? "is-danger" : "is-primary"}`} type="email" placeholder="Email" value={this.state.email} onChange={this.handleChange} name="email"/>
                    <span className="icon is-small is-left">
                        <i className="fas fa-envelope"></i>
                    </span>
                    <span className="icon is-small is-right">
                        {
                          this.state.emailValidationError != null ? (
                            <i className="fas fa-exclamation-triangle"></i>
                          ): ""
                        }
                    </span>
                </div>
                {
                  this.state.emailValidationError != null ? <p className="help is-danger">{this.state.emailValidationError}</p> : ""
                }
            </div>
            <div className="field">
                <div className="control">
                    <label>
                        <input ref={this.input} type="checkbox" className={`${this.state.acceptPrivacyError != null ? "is-danger" : ""}`} checked={this.state.acceptPrivacy} onChange={this.handleChange} name="acceptPrivacy"/>
                        Ich akzeptiere die <a href="/datenschutz">Datenschutzbedingungen</a>
                    </label>
                    {
                      this.state.acceptPrivacyError != null ? <p className="help is-danger">{this.state.acceptPrivacyError}</p> : ""
                    }
                </div>
            </div>
            <div className="field captcha">
              {
                  this.state.captchaResponse != null ? (
                    <>
                      <div className="captcha-message">
                          {this.state.captchaResponse.identifier}
                      </div>
                      <div className="captcha-images">
                      {
                        this.state.captchaResponse.images.map((img, i) => (
                          <img key={i} className={`newsletter2go-captcha-image ${img == this.state.captchaSelection ? "selected": ""}`} onClick={() => this.setState({captchaSelection: img})} src={service.baseUri + `/captcha/image/${img}/${this.state.captchaResponse.keyValue}`} data-hash={img}/>
                        ))
                      }
                      </div>
                    </>
                  ) : <i className="fa fa-spinner fa-spin"></i>
              }
            </div>
            <div className="field">
                <div className="control">
                    <button tabIndex={1} className={`button subscribe-button ${this.state.subscribing == true ? "is-loading" : ""}`} aria-label="Subscribe to newsletter" onClick={this.onSubscribeClicked} disabled={!this.state.acceptPrivacy || this.state.captchaSelection == null}>
                      Anmelden
                    </button>
                </div>
            </div>
            {
              this.state.subscriptionError != null ? (
              <div className="help is-danger">
              {
                this.state.subscriptionError
              }
              </div>
              ) : ""
            }
        </div>
      </Dialog>
    )
  }
  
}

interface N2GSubmitModel
{
  recipient: { email: string };
  captcha: {
    correctValue: string,
    keyValue: string,
    selected: string,
    lists: string[]
  }
}

interface CaptchaResponse
{
  images: string[];
  identifier: string;
  correctValue: string;
  keyValue: string;
}

export class CaptchaValidationFailedError extends Error
{
  
}

export class N2GService
{
  readonly baseUri = "https://api.newsletter2go.com";
  readonly formUri = "5ghc6ehf-5cjsjypj-69s";
  async getCaptcha(): Promise<CaptchaResponse>
  {
    let res = await fetch(urlJoin(this.baseUri, "captcha"), {
      method: "post"
    });
    
    if(!isSuccessStatusCode(res.status))
      throw new Error(`Could not fetch captcha, status: ${res.status}`);
    
    let o = await res.json() as {
      value: {
        images: string[],
        info: {
          correctValue: string;
          identifier: string;
          keyValue: string;
        }
      }[][],
      status: number
    };
    
    if(!isSuccessStatusCode(o.status))
      throw new Error(`Could not fetch captcha, status: ${o.status}`);
      
    let v = o.value[0][0];
    return {
      images: v.images,
      correctValue: v.info.correctValue,
      identifier: v.info.identifier,
      keyValue: v.info.keyValue
    }
  }
  
  async submitSubscription(model: N2GSubmitModel)
  {
    let res = await fetch(urlJoin(this.baseUri, `/forms/submit/${this.formUri}`), {
      method: "post",
      body: JSON.stringify(model)
    });
    
    let o = await res.json() as { status: number, errorMessage: string; info: { count: number } };
    
    if(!isSuccessStatusCode(o.status) || !isSuccessStatusCode(res.status))
    {
      if(o.errorMessage && o.errorMessage.indexOf("Captcha validation failed") >= 0)
        throw new CaptchaValidationFailedError(); 
      throw new Error(`Could not fetch captcha, status: ${res.status}`);
    }
    return o.info.count;
  }
  
}

const service = new N2GService();
