import * as React from "react";
import { FaTimes } from "react-icons/fa";

interface IState
{
  hidden: boolean;
}

interface IProps
{
  header: any;
  className?: string;
}

export class Dialog extends React.Component<IProps, IState>
{
  constructor(props)
  {
    super(props);
    this.state = {
      hidden: true
    }
  }
  
  onKeyDown = (ev: React.KeyboardEvent) => {
    if(ev.key == "Escape")
      this.hide();
  }
  
  onHideClicked = (ev: React.MouseEvent) => {
    this.hide();
  }
  
  show()
  {
    this.setState({
      hidden: false
    });
  }
  
  hide()
  {
    this.setState({
      hidden: true
    });
  }
  
  
  render()
  {
    return (
      <div className="dialog-root" onKeyDown={this.onKeyDown}>
        {
          this.state.hidden == false ? (
            <div className={`dialog-container ${this.props.className}`} onKeyDown={this.onKeyDown}>
              <div className="dialog-content-container">
                  <div className="dialog-header">
                      <div className="dialog-header-left">
                      {
                        this.props.header
                      }
                      </div>
                      <div className="dialog-header-right">
                        <FaTimes onClick={this.onHideClicked}></FaTimes>
                      </div>
                  </div>
                  <div className="dialog-body">
                  {
                    this.props.children
                  }
                  </div>
              </div>
            </div>
          ): ""
        }
      </div>
    )
  }
}