import { CookieService } from './CookieService';
import { SubscribeDialog } from '../components/Subscribe/SubscribeDialog';

class SubscriptionServiceClass
{
  private handlers: ((state: boolean, param?: any) => any)[] = [];
  public subscribed: boolean = false;
  public state: "old"|"new" = null;
  param: any = null;
  
  private subscribeDialog: SubscribeDialog;

  constructor() 
  {
    if(typeof window !== "undefined") //This prevents SSR from accessing document which is undefined during node build
    {
      this.subscribed = CookieService.cookies.subscribed == "true";
    }
  }
  
  assertSubscribeDialog()
  {
    if(this.subscribeDialog == null)
      throw new Error("Subscribe dialog not set"); 
  }
  
  setSubscribeDialog(dialog: SubscribeDialog)
  {
    this.subscribeDialog = dialog;
    this.subscribeDialog.onSuccess = this.onSubscribe;
  }

  async openForm(param: any = null)
  {
    this.assertSubscribeDialog();
    this.param = param;
    await this.subscribeDialog.show();
  }

  closeForm()
  {
    this.assertSubscribeDialog();
    this.subscribeDialog.hide();
  }

  onSubscribe = (cnt) => {
    if(cnt == 1)
    {
      this.state = "new";
    }
    else if(cnt == 0)
    {
      this.state = "old";
    }
    this.setSubscribed();
    this.closeForm();
  }

  showSuccess()
  {
  }

  registerSubscriptionHandler(handler: (state: boolean, param?: any) => any)
  {
    this.handlers.push(handler);
  }

  unregisterSubscriptionHandler(handler: (state: boolean, param?: any) => any)
  {
    let index = this.handlers.indexOf(handler);
    if(index >= 0)
      this.handlers.splice(index, 1);
  }

  setSubscribed()
  {
    this.subscribed = true;
    this.handlers.forEach(h => h(this.subscribed, this.param));
    CookieService.setCookie("subscribed", "true");
  }
}

export const SubscriptionService = new SubscriptionServiceClass();
