import * as React from "react";
import { OutboundLink } from 'react-ga';
import { Link as PrefetchLink } from "gatsby";
import { Link as NoPrefetchLink } from "@reach/router";

interface IProps
{
  href: string;
  eventLabel?: string;
  target?: string;
  rel?: string;
}

class MyOutboundLink extends React.Component<IProps & React.HTMLProps<HTMLAnchorElement>>
{
  readonly defaultEventLabel = "outboundLink";
  readonly defaultTarget = "_blank";
  readonly defaultRel = "nofollow";
  
  render()
  {
    return (
      <OutboundLink to={this.props.href} eventLabel={this.props.eventLabel ?? this.defaultEventLabel} target={this.props.target ?? this.defaultTarget} rel={this.props.rel ?? this.defaultRel} className={this.props.className} id={this.props.id} style={this.props.style}>
        {
          this.props.children
        }
      </OutboundLink>
    )
  }
}

interface IState 
{
  
}

interface ILinkProps
{
  to: string;
  target?: string;
  rel?: string;
  title?: string;
  className?: string;
  activeClassName?: string;
  id?: string;
  style?: any;
  "aria-label"?: string;
  eventLabel?: string;
  isOutbound?: boolean;
  noPrefetch?: boolean;
}

function hasFileExtension(src: string): boolean
{
  let seg = src.split("/");
  let last = seg[seg.length - 1];
  return last && last.indexOf(".") >= 0;
}

export class Link extends React.Component<ILinkProps, IState>
{  
  readonly defaultEventLabel = "outboundLink";
  readonly defaultTarget = "_blank";
  readonly defaultRel = "nofollow";

  constructor(props)
  {
    super(props);
  }
  
  render()
  {
    let isOutbound = this.props.isOutbound ?? false;
    let noPrefetch = this.props.noPrefetch ?? false;
    let to = this.props.to;
    if(to.endsWith("/") == false && hasFileExtension(to) == false)
      to += "/";
    
    return (
      isOutbound == true ? (
        <OutboundLink to={to} eventLabel={this.props.eventLabel ?? this.defaultEventLabel} target={this.props.target ?? this.defaultTarget} rel={this.props.rel ?? this.defaultRel} className={this.props.className} id={this.props.id} style={this.props.style} title={this.props.title} aria-label={this.props["aria-label"]}>
        {
          this.props.children
        }
        </OutboundLink>
      ) : (
        noPrefetch == true ? (
          <NoPrefetchLink to={to} className={this.props.className} id={this.props.id} style={this.props.style} title={this.props.title} aria-label={this.props["aria-label"]}>
          {
            this.props.children
          }
          </NoPrefetchLink>
        ) : (
          <PrefetchLink to={to} className={this.props.className} activeClassName={this.props.activeClassName} id={this.props.id} style={this.props.style} title={this.props.title} aria-label={this.props["aria-label"]}>
          {
            this.props.children 
          }
          </PrefetchLink>
        )
      )
    )
  }
  
}