import { environment } from '../environment';
import { CookieService } from './CookieService';
import { isSuccessStatusCode, urlJoin } from '../tool';

interface IEnvelope<T>
{
  iKey: string;
  time: string;
  name: string;
  data: IEnvelopData<T>;
}

interface IEnvelopData<T>
{
  baseType: "EventData"|"ExceptionData";
  baseData: T;
}

interface IExceptionBaseData
{
  ver: number;
  properties: any;
  exceptions: IExceptionModel[]
}

interface IExceptionModel
{
  typeName: string;
  message: string;
  stack: string;
}

interface ICustomEventModel
{
  properties: any;
  ver: number;
  name: string;
  measurements: any;
}


class AppInsightsServiceClass
{
  private readonly customEventsVersion = 2;
  private initialized = false;
  
  readonly instrumentationKey = "55e88bf4-37a9-460c-8987-fb67016be741";
  readonly baseUri = "https://dc.services.visualstudio.com";
  
  constructor()
  {
    console.log(process.env.NODE_ENV);
    if(typeof window !== "undefined")
    {
      window.addEventListener("error", (e) => {
        this.trackError(`Uncaught exception`, e.error);
      })
    }
  }
  
  track(model: IEnvelope<any>)
  {
    if(environment.isDev == true)
      return;
    
    let status;
    try
    {
      fetch(urlJoin(this.baseUri, "v2/track"), {
        method: "post",
        body: JSON.stringify(model)
      })
      .then((res) => {
        status = res.status;
        if(!isSuccessStatusCode(res.status))
          throw new Error();
      })
      .catch(e => {
        throw e;
      });
    }
    catch(err)
    {
      console.error(`Error tracking exception, status: ${status}`);
    }
  }
  
  trackEvent(name: string, measurements: any)
  {
    let model: IEnvelope<ICustomEventModel> = {
      iKey: this.instrumentationKey,
      name: `Microsoft.ApplicationInsights.${this.instrumentationKey}.Event`,
      time: new Date().toISOString(),
      data: {
        baseType: "EventData",
        baseData: {
          ver : this.customEventsVersion,
          properties: {
            version: this.customEventsVersion,
          },
          name,
          measurements
        }
      }
    }
  }
  
  trackError(message: string, error: Error)
  {
    let model: IEnvelope<IExceptionBaseData> = {
      iKey: this.instrumentationKey,
      name: "ExceptionData",
      time: new Date().toISOString(),
      data: {
        baseType: "ExceptionData",
        baseData: {
          ver : this.customEventsVersion,
          properties: {
            version: this.customEventsVersion,
            message
          },
          exceptions: [
            {
              message: error.message,
              stack: error.stack,
              typeName: error.name
            }
          ]
        }
      }
    }
    this.track(model);
  }
}


export const AppInsightsService = new AppInsightsServiceClass();
